.forming{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 340px;
    margin: auto;
    position: relative;
    top: 100px;
    height: 410px;
    padding: 0px;
    
}
.name input{
    height: 26px;
    width: 220px;
    margin-bottom: 19px;
    border-radius: 4px;
    padding: 5px  10px 5px 40px;
    font-weight: bold;
     background-color: white;
     border-radius: 5px;
     border: 1px solid #3C2A21;
    color: #3C2A21;
   
    font-size: 16px;
}


.email input{
    height: 25px;
    width: 220px;
    margin-left: 35px;
}

.name{
    margin-left: 35px;
}

.forming h3{
 margin-left: 70px;
 font-weight: bolder;
 font-size: 1.4rem;
 color: #1A120B;
}
button[type="submit"] {
    padding: 10px 20px;
    
    border-radius: 5px;
    cursor: pointer;
    /* transition: 0.4s; */
     background-color: #1A120B ;
      color: #E5E5CB;
    font-weight: bold;
    font-size: 1rem;
    width: 210px;
    border-radius: 100px;
    margin: auto;
    
    
}
